import { Component, OnInit } from '@angular/core';

import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import * as moment from 'moment';

import { Helper, Library, SocketService, AuthService, VStringService, VPreviewInvService, sEnum } from '@core/index';
import { VatService } from '@modules/vat/shared';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { VoucherService } from '@modules/vouchers/shared';
import { ViewInvoiceService } from '@shared/service/view-invoice.service';
@Component({
  selector: 'app-sign-many',
  templateUrl: './sign-many.component.html',
  styleUrls: ['./sign-many.component.scss']
})
export class SignManyComponent implements OnInit {

  licenses: any;
  configs: any;
  ioSignature: any;
  licenseList: any;
  licenseIndex = 0;
  tempKiFile = 0;
  tempTemplate: any;
  daXong: boolean = false;
  ganXong: boolean = false;

  valueProgress: number = 0;
  errorImport: boolean = false;
  subXuatHoaDon: any;
  showPopup = false;
  totalInvoice: number;
  _translate: any;
  nodeSign: any;
  isExportFailed: boolean = false;
  reportCountERR: any = "";
  reportCountTXN: any = "";
  isTT78: boolean = false;
  infoDangKy: any;
  isHdGTGT: any;
  currentUser: any;
  taxCode: any;
  ids: any;
  checkAll: boolean = false;
  format = (value) => {
    let percent = Math.round(value * 100);
    return this._translate.NOTIFY.finish + ": " + percent + '%';
  }
  letter: any;
  constructor(
    public bsModalRef: BsModalRef,
    private vatService: VatService,
    private socketService: SocketService,
    private authService: AuthService,
    private router: Router,
    private spinner: NgxSpinnerService,
    private voucherService: VoucherService,
    private vPreviewInvService: ViewInvoiceService
  ) {
    this._translate = this.authService.getTranslate();
  }

  ngOnInit() {
    this.configs = this.authService.getConfigs();
    this.infoDangKy = this.authService.getInfoRegister();
    this.currentUser = this.authService.getCurrentUser();
    if (this.currentUser.account) {
      this.taxCode = this.currentUser.account.tax_code;
    }
    if (this.infoDangKy) {
      if (this.infoDangKy.status == "CHAP_NHAN")
        this.isTT78 = true;
    }
    this.convertData();
    this.connectWebsocket();
  }

  convertData() {
    this.licenseList = this.licenseList.sort((itemA, itemB) => {
      return new Date(itemA.license_date).getTime() - new Date(itemB.license_date).getTime();
    });
    this.licenseList.forEach(item => {
      item.license_date = moment(item.license_date).format('YYYY-MM-DD HH:mm:ss');
      delete item.updated_at;
      delete item.decpoint_is_dot;
      delete item.money_decimal_format;
      item["checkTXN"] = false;
    });
  }

  onImportShowing(e) {
    this.valueProgress = 0;
  }

  onImportHiding(e) {
    let r = true;
    if (this.valueProgress < 100 && this.errorImport == false) {
      r = confirm(this._translate.CONFIRM.sign_stop);
    }
    if (r) {
      if (this.subXuatHoaDon) {
        this.subXuatHoaDon.unsubscribe();
      }
      this.daXong = true;
      this.totalInvoice = 0;
      this.valueProgress = 0;
    } else {
      e.cancel = true;
    }
  }

  connectWebsocket() {
    this.ioSignature = this.socketService.lapHoaDonImport().subscribe(message => {
      let data: any = message;
      if (data.Status == 'success') {
        if (data.SvcName == "efy_Sign") {
          let i = this.licenseIndex;
          if (data.Data.Name == 'hoadon.xml') {
            this.goiApiXuatHoaDon(data.Data.Content, this.licenses[i]);
          } else if (data.Data.Name == 'ten_filek_the_trung-dc-nhi.xml') {
            this.licenses[i].issuer_name = data.Data.IssuerName;
            this.xuLiApiKhiXuat(this.licenses[i]);
          } else if (data.Data.Name === 'thu_xac_nhan.pdf') {
            this.goiApiXuatHoaDonThu(data.Data.Content);
          } else {
            this.licenses[i].invoice_files.forEach(item => {
              if (item.file_name == data.Data.Name) {
                item.file_content = data.Data.Content;
              }
            });
            this.tempKiFile++;
            if (this.tempKiFile == this.licenses[i].invoice_files.length) {
              this.xuLiApiKhiXuat(this.licenses[i]);
            }
          }
        }
      } else {
        const popUp = window.open('about:blank', 'name', 'width=200, height=100');
        if (popUp == null || typeof (popUp) == 'undefined') {
        }
        else
          popUp.close();
        this.spinner.hide();
        if (data.Description == "") {
          this.showPopup = false;
          this.errorImport = true;
        }
        else {
          if (data.Data.Name === 'thu_xac_nhan.pdf')
            this.licenseList[this.licenseIndex].license_confirm_letters[0].status = data.Description;
          else
            this.licenseList[this.licenseIndex].status = data.Description;
          this.isExportFailed = true;
          this.continuteExportInvoice(false);
        }
      }
    });
  }
  continuteExportInvoice(isSuccess) {
    let percentComplete = 100 / this.totalInvoice;
    this.valueProgress += percentComplete;
    if (this.licenseIndex == this.totalInvoice - 1) {
      this.reportCountERR = "";
      this.reportCountTXN = "";
      if (!this.isExportFailed && isSuccess)
        Library.notify(this._translate.NOTIFY.sign_success, 'success', 5000);
      else {
        let lstInvoiceERR = this.licenseList.filter(p => p.status != "DA_XUAT" && p.status != "GHI_TAM");
        this.reportCountERR = "C " + lstInvoiceERR.length + " chng t k li.  ngh kim tra li!";
        Library.notify("C " + lstInvoiceERR.length + " chng t k li", 'success', 5000);
      }
      this.daXong = true;
      setTimeout(() => {
        this.showPopup = false;
        // this.voucherService.getInvoicesWaitSign(this.ids).subscribe(res => {
        //   this.licenseList = res.data.licenses
        // })
      }, 300);
      try {
        let dsTxnErr = this.licenseList.filter(p => p['checkTXN'] && p.license_confirm_letters[0].status != 'DA_XUAT' && p.license_confirm_letters[0].status != 'GHI_TAM');
        if (dsTxnErr && dsTxnErr.length > 0)
          this.reportCountTXN = "C " + dsTxnErr.length + " th xc nhn k li.  ngh kim tra li!";
      }
      catch (error) {

      }
    }
    else if (this.licenseIndex < this.totalInvoice - 1) {
      this.licenseIndex++;
      this.xuatHoaDon();
    }
  }
  xuLyKiFile(xmlLicense, signDate) {
    this.socketService.kiSo(() => this.connectWebsocket(), xmlLicense, 'hoadon.xml', this.nodeSign, signDate, 'LicenseData', '', true, this.taxCode, true);
  }

  xuLiApiKhiXuat(license) {
    let tempTemplate = license.template;
    let xmlInvoice = "";
    let isNewXml = 0;
    let licenseClone = Object.assign({}, license);
    delete licenseClone.checkTXN;
    delete licenseClone.template;
    delete licenseClone.license_confirm_letters;
    delete licenseClone.license_number_tempt;
    let sub = this.voucherService.edit(licenseClone.id, licenseClone, true).subscribe(rs => {
      license.template = tempTemplate;
      if (rs.status == 'success') {
        license.license_date = moment(rs.data.license_date).format('YYYY-MM-DD HH:mm:ss');
        this.licenseList[this.licenseIndex]["license_number_tempt"] = rs.data.license_number;
        isNewXml = rs.data.is_new_xml;
        this.nodeSign = "//CTu/DSCKS/TCTTNhap";
        xmlInvoice = VStringService._getValueToString(rs.data.xml_string);
        if (xmlInvoice != "") {
          if (this.configs.integrate_parameter.concentrate_signature.is_concentrate_signature
            || (this.configs.integrate_parameter.remote_signing && this.configs.integrate_parameter.remote_signing.is_remote_signing)) {
            this.goiApiXuatHoaDon(xmlInvoice, license);
          } else {
            this.xuLyKiFile(xmlInvoice, license.invoice_date);
          }
        }
      } else {
        if (this.licenseList[this.licenseIndex].status != 'DA_XUAT')
          this.licenseList[this.licenseIndex].status = rs.message;
        this.isExportFailed = true;
        this.continuteExportInvoice(false);
      }
    }, err => { }, () => {
      sub.unsubscribe();
    });

  }

  async goiApiXuatHoaDon(xml_string, license) {
    // Helper.dowloadFile('hoadon.xml', 'xml', xml_string);
    this.subXuatHoaDon = this.voucherService.xuatChungTu({
      id: license.id,
      xml_string: xml_string,
      license_files: license.license_files
    }).subscribe(async rs => {
      let isSuccess = false;
      this.ganXong = true;
      this.tempKiFile = 0;
      let isKyTXN = false;
      this.letter = null;
      if (rs.status == 'success') {
        this.licenseList[this.licenseIndex].license_number = this.licenseList[this.licenseIndex]["license_number_tempt"];
        this.licenseList[this.licenseIndex].status = 'DA_XUAT';
        isSuccess = true;
        if (license["checkTXN"])
          isKyTXN = true;
      } else {
        if (this.licenseList[this.licenseIndex].status != 'DA_XUAT')
          this.licenseList[this.licenseIndex].status = rs.message;
        this.isExportFailed = true;
      }
      if (isKyTXN) {
        this.letter = license.license_confirm_letters[0];
        let isTaoThanhCong = await this.taoThuXacNhan();
        if (isTaoThanhCong)
          this.kyThuXacNhan();
        else
          this.continuteExportInvoice(isSuccess);
      }
      else
        this.continuteExportInvoice(isSuccess);
    }, err => { }, () => {
      // Library.hideloading();
      this.subXuatHoaDon.unsubscribe();
    });
  }
  async taoThuXacNhan() {
    return await this.voucherService.updateGhiTamThuXacNhan(this.letter, this.letter.id).toPromise().then(rs => {
      if (rs.status === 'success') {
        this.letter['file_content'] = rs.data.pdf_file.file_content;
        this.letter['sign_date'] = moment(rs.data.sign_date).format('YYYY-MM-DD HH:mm:ss');
        return true;
      } else {
        return false;
      }
    },
      err => { return false; }
    ).catch((error) => {
      return false;
    });
  }
  kyThuXacNhan() {
    if (this.configs.integrate_parameter.concentrate_signature.is_concentrate_signature
      || (this.configs.integrate_parameter.remote_signing && this.configs.integrate_parameter.remote_signing.is_remote_signing)) {
      this.goiApiXuatHoaDonThu(this.letter['file_content']);
    } else {
      this.xuLyKiFileThu(this.letter['file_content'], this.letter['sign_date']);
    }
  }
  goiApiXuatHoaDonThu(data) {
    const license_confirm_letter = {
      id: this.letter.id,
      pdf_file: {
        file_name: "thu_xac_nhan.pdf",
        file_content: data
      }
    }
    setTimeout(function () {
      Library.showloading();
    }, 20);
    const sub = this.voucherService.xuatTXN(license_confirm_letter).subscribe(rs => {
      if (rs.status == 'success') {
        this.licenseList[this.licenseIndex].license_confirm_letters[0].status = 'DA_XUAT';
      } else {
        this.licenseList[this.licenseIndex].license_confirm_letters[0].status = rs.message;
      }
      this.continuteExportInvoice(true);
    }, err => {
      this.continuteExportInvoice(true);
    }, () => {
      Library.hideloading();
      sub.unsubscribe();
    });
  }
  xuLyKiFileThu(xmlInvoice, signDate?) {
    // this.connectWebsocketThu();
    let nodeSign = '//CTu/DSCKS/TCTTNhap';
    this.connectWebsocket();
    if (this.socketService.checkNull()) {
      this.socketService.connect();
    }
    this.socketService.kiFile(() => this.connectWebsocket(), xmlInvoice, 'thu_xac_nhan.pdf', 'pdf', nodeSign, signDate, 'ALIGN_LEFT', 'PAGE_LAST', null, this.taxCode);
  }
  xuatHoaDon() {
    let i = this.licenseIndex;
    if (this.configs.integrate_parameter.concentrate_signature.is_concentrate_signature
      || (this.configs.integrate_parameter.remote_signing && this.configs.integrate_parameter.remote_signing.is_remote_signing)) {
      this.xuLiApiKhiXuat(this.licenses[i]);
    } else {
      if (this.licenses[i].license_files && this.licenses[i].license_files.length) {
        this.licenses[i].license_files.forEach(item => {
          this.socketService.kiFile(() => this.connectWebsocket(), item.file_content, item.file_name, item.file_name.split('.').pop(), true, this.licenses[i].license_date);
        });
      }
      else {
        let invoiceDate = moment(new Date()).format('YYYY-MM-DD HH:mm:ss');
        this.socketService.kiSo(() => this.connectWebsocket(), 'PHRlbXBJbnZvaWNlPjwvdGVtcEludm9pY2U+', 'ten_filek_the_trung-dc-nhi.xml', '//tempInvoice', invoiceDate, "", '', true, this.taxCode, true);
      }
    }
  }

  kyHangLoat() {
    Library.confirmVA(this._translate.THONG_TIN.dong_y_ky_so, this._translate.NOTIFY.notify, () => {
      this.showPopup = true;
      this.licenses = JSON.parse(JSON.stringify(this.licenseList));
      this.totalInvoice = this.licenses.length;
      this.licenseIndex = 0;
      this.tempKiFile = 0;
      this.xuatHoaDon();
    });
  }

  dong() {
    if (this.ganXong) {
      this.bsModalRef.content.successSaveItem = "success";
    }
    this.bsModalRef.hide();
  }

  viewDetailInvoice(data) {
    if (data) {
      const ids = [];
      if (data) {
        ids.push(data.id);
      }
      let dulieu = this.voucherService.taodulieuXemMau(ids);
      dulieu['configs'] = sEnum.cauhinhxemmau.chungtu;
      this.vPreviewInvService.xemMau(dulieu);
    } else {
      Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
    }
  }
  kiemtraTXN(data) {
    if (data.license_confirm_letters && data.license_confirm_letters.length > 0)
      return true;
    return false;
  }
  changeCheckAll($event) {
    this.checkAll = $event.target.checked;
    this.licenseList.forEach(item => {
      if (this.kiemtraTXN(item)) {
        item["checkTXN"] = $event.target.checked;
      }
    });
  }
  changeCheckItem($event) {
    if (!$event.target.checked && this.checkAll)
      this.checkAll = false;
    else if ($event.target.checked && !this.checkAll) {
      let itemCheck = this.licenseList.some(p => this.kiemtraTXN(p) && !p["checkTXN"]);
      if (!itemCheck)
        this.checkAll = true;
    }
  }
}
