  /* Ghi tm */
  ghiTam(data) {
    if (this.frmData.id) {
      Library.showloading();
      const sub = this.voucherService.updateGhiTamThuXacNhan(data, this.frmData.id).subscribe(rs => {
        if (rs.status === 'success') {
          this.frmData.id = rs.data.id;
          Library.notify(this._translate.NOTIFY.save_invoice_success, 'success');
          this.setDataSessionStorage(this.frmData);
        } else {
          Library.notify(rs.message, 'error');
        }
      }, err => {
      }, () => {
        Library.hideloading();
        sub.unsubscribe();
      });
    } else {
      Library.showloading();
      const sub = this.voucherService.ghiTamThuXacNhan(data).subscribe(rs => {
        if (rs.status === 'success') {
          this.frmData.id = rs.data.id;
          Library.notify(this._translate.NOTIFY.save_invoice_success, 'success');
          // if (this.frmData.license_id && this.frmData.id) {
          // this.router.navigate([`/system/voucher-tax/${this.frmData.license_id};idTXN=${this.frmData.id}`])
          // this.router.navigate([`/system/voucher-tax/${VStringService._getValueToString(this.frmData.license_id)}`, { idTXN: this.frmData.id }]);
          // }
          this.setDataSessionStorage(this.frmData);
        } else {
          Library.notify(rs.message, 'error');
        }
      }, err => {
      }, () => {
        Library.hideloading();
        sub.unsubscribe();
      });
    }
  }

