  onChangeTem(e) {
    this.select.templatesTT78.forEach(item => {
      if (e.value === item.value) {
        this.fields = item.fields
        this.frmData.template_id = item.value;
        for (const key in item) {
          if (item.hasOwnProperty(key)) {
            const value = item[key];
            this._oTemplateTT78[key] = value;
          }
        }
        // Nu l mu html
        if (item.fields) {
          this.formItems = [];
          this.customForms = [];
          const _arrayGrid = item.fields.filter(x => x.type === '5');
          if (_arrayGrid && _arrayGrid.length > 0) {
            this.extentionGrid.ma_hang = false;
            this.extentionGrid.so_lo = false;
            this.extentionGrid.han_dung = false;
            this.extentionGrid.nuoc_san_xuat = false;
            this.extentionGrid.don_hang_hop_dong = false;
            this.extentionGrid.ma_quan_ly = false;
            for (let i = 0; i < _arrayGrid.length; i++) {
              const column = _arrayGrid[i]['name'];
              const value = _arrayGrid[i]['is_show'];
              const _ma = VStringService._convertToViKdau(column);
              this.extentionGrid[VStringService._convertToViKdau(column)] = value;
            }
          }
          const _dulieumorong = item.fields.filter(x => x.type === '4');
          if (_dulieumorong.length > 0) {
            _dulieumorong.forEach(item1 => {
              if (item1.is_show) {
                if (item1.data_type === 'chu') {
                  this.customForms.push(
                    {
                      cssClass: 'custom-input',
                      editorType: 'dxTextBox',
                      dataField: item1.code,
                      label: { text: item1.name },
                      editorOptions: {
                        onValueChanged: () => {
                          this.changeItem(_dulieumorong);
                        }
                      }
                    }
                  );
                }
                if (item1.data_type === 'soluong') {
                  this.customForms.push(
                    {
                      cssClass: 'custom-input',
                      editorType: 'dxNumberBox',
                      dataField: item1.code,
                      label: { text: item1.name },
                      editorOptions: {
                        onValueChanged: () => {
                          this.changeItem(_dulieumorong);
                        },
                        format: this.formatter.thu_xac_nhan_thu_nhap,
                        showClearButton: true
                      }
                    }
                  );
                }
                if (item1.data_type === 'ngay') {
                  this.customForms.push(
                    {
                      cssClass: 'custom-input',
                      editorType: 'dxDateBox',
                      dataField: item1.code,
                      label: { text: item1.name },
                      editorOptions: {
                        onValueChanged: () => {
                          this.changeItem(_dulieumorong);
                        }, displayFormat: 'dd/MM/yyyy', dateSerializationFormat: 'yyyy-MM-dd HH:mm:ss',
                      }
                    }
                  );
                }
                if (item1.data_type === 'danhmuc') {
                  this.customForms.push(
                    {
                      cssClass: 'custom-input',
                      editorType: 'dxSelectBox',
                      dataField: item1.code,
                      label: { text: item1.name }
                    }
                  );
                }
              }
            });
            this.formItems.push(this.createCustomGroup());
            this.frmData.license_confirm_letter_options = _dulieumorong;
          }
          else
            this.frmData.license_confirm_letter_options = [];
          _dulieumorong.forEach(value => {
            this.customerTempt[value.code] = value.value;
          });
        } else {
          this.formItems = [];
          this.customerTempt = {};
          this.extentionGrid.ma_hang = false;
          this.extentionGrid.so_lo = false;
          this.extentionGrid.han_dung = false;
          this.extentionGrid.nuoc_san_xuat = false;
          this.extentionGrid.don_hang_hop_dong = false;
          this.extentionGrid.ma_quan_ly = false;
        }
      }
    });
  }
