  // Ly gi tr m rng(dng key:value)
  layGiaTriMoRong(data, type = '') {
    let arrMoRong = this.templatesOptions.concat(this.templatesTXNOptions);
    let result = {};
    arrMoRong.forEach(column => {
      const col = column.code;
      let val: any = '';
      if (type) {

      } else {
        if (data.hasOwnProperty(column.code)) {
          switch (column.data_type) {
            case 'ngay':
              val = Helper.processDate(data[col]);
              break;
            case 'soluong':
              val = data[col] ? Helper.toNumber(data[col]) : null;
              break;
            default:
              val = data[col];
          }
        }
      }
      result[col] = val;
    })
    return result
  }

  //Ly gi tr cc trng m rng y 
  layGiaTriMoRongDayDu(data, type = '') {
    let arrGtmr = this.templatesOptions.concat(this.templatesTXNOptions);
    let result = [];
    if (type) {
      switch (type) {
        case 'template':
          arrGtmr = this.templatesOptions;
          break;
        case 'templateTXN':
          arrGtmr = this.templatesTXNOptions;
          break
      }
    }
    arrGtmr.forEach(item => {
      let value = data[item.code] ? data[item.code] : null
      if (item.data_type == 'ngay' && value) {
        value = moment(value).format('YYYY-MM-DD HH:mm:ss')
      }
      result.push({
        code: item.code,
        data_type: item.data_type,
        name: item.name,
        value: value
      })
    })

    return result;
  }



  //Kim tra c nhp mu th XN khng ?
  checkTXN(letter) {
    const dataLetter = Object.keys(letter).filter(item => !this.fieldsKTTTXN.includes(item))
    let exists = dataLetter.filter(item => letter.hasOwnProperty(item) && letter[item] !== null && letter[item] !== '' && letter[item] != undefined)
    let status = false;
    if (exists.length > 1) {
      status = true;
    }
    if (!status && letter.license_confirm_letter_options) {
      if (letter.license_confirm_letter_options.find(o => o.value !== null && o.value !== '' && o.value != undefined))
        return true
    }
    return status;
  }

