  converArrayToObject(arr, key, value) {
    let obj = {};
    arr.forEach(item => {
      obj[item[key]] = item[value];
    })
    return obj;
  }

  changeTemplateInvoice(strDate) {
    let year = "";
    if (VStringService._getValueToString(strDate) != "")
      year = VStringService._getValueToString(strDate).substring(4, 2);
    else {
      let invoiceDate = moment(new Date()).format('YYYY-MM-DD HH:mm:ss');
      year = VStringService._getValueToString(invoiceDate).substring(4, 2);
    }
    let templates_temp = this.templates;
    this.templates = [];
    if (templates_temp && templates_temp.length > 0) {
      templates_temp.forEach(item => {
        if (VStringService._getValueToString(item.name) != "")
          item.name = item.name.replace(item.name.substring(15, 13), year);
        this.templates.push(item);
      });
    }

  }
