  ngOnInit() {
    this.connectWebsocket();
    this.currentUser = this.authService.getCurrentUser();
    if (this.currentUser.account) {
      this.taxCode = this.currentUser.account.tax_code;
      if (this.currentUser.account.van_account_info)
        this.province_name = this.currentUser.account.van_account_info.city_name;
    }
    if (this.route.snapshot.paramMap.get('isThuXacNhan')) {
      this.selectedIndex = 1;
      this.isVoucher = false;
      this.isIncomeConfirmation = true;
    }
    this.routeSub = this.route.data.subscribe((data: any) => {
      // get cu hnh
      let danh_muc = this.authService.getDMLocal();
      this.configs = this.authService.getConfigs();
      this.oldConfigs = _.cloneDeep(this.configs.system_parameter);
      this.formatter = VChanggeMoneyService.getFormatterColumnInv(this.configs.decimal_places_json);
      this.frmData.display_currency = JSON.stringify(this.configs.display_currency);
      this.formatter.display_int = this.configs.system_parameter.display_int;
      this.select.unit_codes = danh_muc.DM_DON_VI_TINH ? danh_muc.DM_DON_VI_TINH.data : null;
      this.select.select_provisions_on_accounts = danh_muc.DM_DINH_KHOAN ? danh_muc.DM_DINH_KHOAN.data : null;
      this.select.payment_method_names = danh_muc.DM_HINH_THUC_THANH_TOAN ? danh_muc.DM_HINH_THUC_THANH_TOAN.data : null;
      this.select.vats = danh_muc.DM_TY_LE_VAT ? danh_muc.DM_TY_LE_VAT.data : null;
      this.select.countrys = danh_muc.DM_QUOC_GIA ? danh_muc.DM_QUOC_GIA.data : null;
      this.select.cities = danh_muc.DM_TINH_THANH ? danh_muc.DM_TINH_THANH.data : null;
      let isEdit = false;
      let invoice: any;
      if (data.init[1] == true)
        isEdit = true;
      if (isEdit) {
        this.select.templatesTT78 = data.init[2].filter(item => item.template_type !== 'THU_XAC_NHAN_THU_NHAP');
        invoice = data.init[0];
      }
      else {
        this.select.templatesTT78 = data.init[0].filter(item => item.template_type !== 'THU_XAC_NHAN_THU_NHAP');
      }
      if (isEdit || this.voucherService.isCopy) {
        if (this.voucherService.isCopy) {
          this.khoiTaoKhiSua(this.voucherService.data);
        } else {
          // this.isSuaGhiTam = true;
          this.khoiTaoKhiSua(invoice);
        }
      } else {
        this.khoiTaoKhiThemMoi();
      }
      if (this.configs.system_parameter.voucher_currency) {
        this.frmData.currency_code = this.configs.system_parameter.voucher_currency;
      }
      this.changeValueTempt();
      // localStorage.setItem('lct', JSON.stringify(this.frmData))
      setTimeout(() => {
        this.setDataSessionStorage(this.frmData);
        this.changeTemplateInvoice(this.frmData.license_date)
      });
    });
    this.listpermissions = this.currentUser.user.permissions.split(',');
    const _obj = this.listpermissions.filter(p => p === 'phe_duyet');
    if (_obj.length > 0) {
      this.isAccessExportInv = true;
    } else {
      this.isAccessExportInv = false;
    }
  }
