import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { IndexComponent } from '@modules/vouchers/components/index/index.component';
import { VoucherComponent } from '@modules/vouchers/components/voucher/voucher.component';
import { VatDeactivateGuard } from '@modules/vat/deactivate/vat-deactivate.guard';
import { VoucherResolver, ShowVoucherResolver } from './shared';

const routes: Routes = [
  { path: '', component: IndexComponent },
  {
    path: 'create',
    component: VoucherComponent,
    canDeactivate: [VatDeactivateGuard],
    resolve: { init: VoucherResolver },
    data: { brec: 'create_voucher' }
  },
  {
    path: 'edit',
    component: VoucherComponent,
    canDeactivate: [VatDeactivateGuard],
    resolve: { init: ShowVoucherResolver },
    data: { brec: 'voucher_details' }
  },
  {
    path: ':voucherId',
    component: VoucherComponent,
    canDeactivate: [VatDeactivateGuard],
    resolve: { init: ShowVoucherResolver },
    data: { brec: 'voucher_details' }
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class VouchersRoutingModule { }
